<template>
  <div class="mineadress" v-loading="loading">
    <div class="title">
      <h1></h1>
    </div>
    <div class="myAddress" v-if="list.length == 0"></div>
    <div class="myAddress" v-for="item in list" :key="item.id">
      <div class="myAddressTitle">{{item.name}}</div>
      <div class="myAddressName">{{item.name}}</div>
      <div class="myAddressHome">{{item.address}}</div>
      <div class="myAddressPhone">{{item.rec_phone}}</div>
      <div class="delete" @click="deleteAddress(item.id)"></div>
      <div class="edit" @click="information(item.id)"></div>
      <div class="edit" v-if="item.status == 0" @click="setDefault(item.id)"></div>
      <div class="edit" v-if="item.status == 1"></div>
    </div>
    <div class="add_box" @click="clickAdd"></div>
    <div class="jumpbox" v-show="display">
      <div class="con_left">
        <input type="text" placeholder="">
        <br>
        <input type="text" placeholder="">
      </div>
      <div class="con_right">
        <input type="text" placeholder="//">
        <br>
        <VDistpicker class="selec"/>
        <input type="text" placeholder="">
      </div>
      <div class="btn" @click="jumpback"></div>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <div class="editAddessInput"></div>
      <el-input v-model="editAddress.name" placeholder=""></el-input>
      <div class="editAddessInput"></div>
      <el-input v-model="editAddress.address" placeholder=""></el-input>
      <div class="editAddessInput"></div>
      <el-input v-model="editAddress.rec_phone" placeholder=""></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button type="primary" @click="ditAddress"></el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="isdialogFormVisible">
      <div class="editAddessInput"></div>
      <el-input v-model="addAddress.name" placeholder=""></el-input>
      <div class="editAddessInput"></div>
      <el-input v-model="addAddress.address" placeholder=""></el-input>
      <div class="editAddessInput"></div>
      <el-input v-model="addAddress.phone" placeholder=""></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isdialogFormVisible = false"></el-button>
        <el-button type="primary" @click="isaddAddress"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import VDistpicker from "v-distpicker";
import { log } from "util";
export default {
  components: { VDistpicker },
  data() {
    return {
      loading: true,
      display: false,
      dialogTableVisible: false,
      dialogFormVisible: false,
      isdialogTableVisible: false,
      isdialogFormVisible: false,
      list: [
        {
          id: "",
          name: "",
          address: "",
          phone: ""
        }
      ],
      editAddress: {
        id: "",
        name: "",
        address: "",
        rec_phone: ""
      },
      addAddress: {
        name: "",
        address: "",
        phone: ""
      }
    };
  },
  created() {
    this.getAddress();
  },
  methods: {
    jump() {
      this.display = true;
    },
    jumpback() {
      this.display = false;
    },
    //
    getAddress() {
      this.loading = true;
      //sess
      let userString = sessionStorage.getItem("user");
      //iddata
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/user/queryRecInfo", {
          params: {
            user_phone: this.user_id
          }
        })
        .then(res => {
          this.list = res.data.data;
          this.loading = false;
        });
    },
    //idid
    deleteAddress(id) {
      console.log(id);
      this.$axios
        .post("/user/removeRecInfo", {
          id: id
        })
        .then(res => {
          this.getAddress();
        });
    },
    //
    information(id) {
      console.log(this);
      this.list.forEach(item => {
        if (item.id == id) {
          (this.editAddress.id = item.id),
            (this.editAddress.name = item.name),
            (this.editAddress.address = item.address),
            (this.editAddress.rec_phone = item.rec_phone);
        }
      });
      this.dialogFormVisible = true;
      console.log(id);
    },
    //
    open(title, message) {
      this.$alert(message, title, {
        confirmButtonText: "",
        callback: action => {}
      });
    },
    //
    clickAdd() {
      console.log("in");
      this.isdialogFormVisible = true;
    },
    //
    ditAddress() {
      //sess
      let userString = sessionStorage.getItem("user");
      //iddata
      this.user_id = JSON.parse(userString).phone;
      if (
        this.editAddress.name == "" ||
        this.editAddress.rec_phone == "" ||
        this.editAddress.address == ""
      ) {
        this.open("", "");
      } else {
        this.$axios
          .post("/user/modifyRecInfo", {
            id: this.editAddress.id,
            name: this.editAddress.name,
            user_phone: this.user_id,
            rec_phone: this.editAddress.rec_phone,
            address: this.editAddress.address
          })
          .then(res => {
            this.getAddress();
            this.dialogFormVisible = false;
          });
      }
    },
    //
    isaddAddress() {
      //sess
      let userString = sessionStorage.getItem("user");
      //iddata
      this.user_id = JSON.parse(userString).phone;
      if (
        this.addAddress.name === "" ||
        this.addAddress.phone == "" ||
        this.addAddress.address === ""
      ) {
        this.open("", "");
      } else {
        this.$axios
          .post("/user/addRecInfo", {
            name: this.addAddress.name,
            user_phone: this.user_id,
            rec_phone: this.addAddress.phone,
            address: this.addAddress.address
          })
          .then(res => {
            this.getAddress();
            this.isdialogFormVisible = false;
            this.addAddress.name = "";
            this.addAddress.address = "";
            this.addAddress.phone = "";
          });
      }
    },
    //
    setDefault(id) {
      this.$axios
        .post("/user/modifyRecInfoDefaultStatus", {
          id: id
        })
        .then(res => {
          this.getAddress();
        });
    }
  }
};
</script>

